import { Component, OnInit } from '@angular/core';

import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-train',
  templateUrl: './train.component.html',
  styleUrls: ['./train.component.css']
})
export class TrainComponent implements OnInit {
  nearTrains: any[];
  geolocationPosition: Position;
  msg: any;

  constructor(
    private authService: AuthService
  ) { }

  ngOnInit() {
    if (window.navigator && window.navigator.geolocation) {
      window.navigator.geolocation.getCurrentPosition(
          position => {
              this.geolocationPosition = position,
            console.log(`${position.coords.latitude}, ${position.coords.longitude}`);
     this.authService.getNearlyTrain(position.coords.latitude, position.coords.longitude).subscribe(train => {
         // tslint:disable-next-line:triple-equals
         if (!train.success) {this.nearTrains = null; this.msg = train.msg;
        // tslint:disable-next-line:triple-equals
        } else {
      this.nearTrains = train.data;
      console.log(this.nearTrains); }
     });
          },
          error => {
              switch (error.code) {
                  case 1:
                      console.log('Permission Denied');
                      break;
                  case 2:
                      console.log('Position Unavailable');
                      break;
                  case 3:
                      console.log('Timeout');
                      break;
              }
          }
      );
  }

  }
}
